/* ************************************************ */
/* RISK ANALYSIS */
/* ************************************************ */
.risk-analysis{
    /* height: 100vh; */
    width: 100%;
    box-sizing: border-box;
    text-align: center;
  }
  img {
    vertical-align: middle;
  }
  .container {
    position: relative;
  }
  .mySlides {
    display: none;
  }
  .prev{
    cursor: pointer;
    position: absolute;
    top: 20vh;
    width: auto;
    padding: 10px;
    left: 0%;
    color: var(--dark-blue);
    font-weight: bold;
    font-size: 40px;
    border-radius: 0 3px 3px 0;
    user-select: none;
    -webkit-user-select: none;
  }
  .next {
    cursor: pointer;
    position: absolute;
    top: 20vh;
    width: auto;
    padding: 10px;
    right: 0%;
    color: var(--dark-blue);
    font-weight: bold;
    font-size: 40px;
    border-radius: 3px 0 0 3px;
    user-select: none;
    -webkit-user-select: none;
  }
  .prev:hover,
  .next:hover {
    background-color: var(--light-blue);
    color: white;
  }
  .numbertext {
    color: #ffffff;
    font-size: 20px;
    padding: 8px 12px;
    position: absolute;
    top: 0;
    font-family: 'El Messiri', sans-serif;
  }
  .caption-container p{
    text-align: center;
    background-color: var(--light-blue);
    padding: 2px 16px;
    color: white;
    font-size: 18px;
    font-family: 'El Messiri', sans-serif;
  }
  .slide-row:after {
    content: "";
    display: table;
    clear: both;
  }
  .slide-row{
    display: flex;
}
  .risk-column {
    float: left;
    width: 15vw;
    border-right: 2px solid whitesmoke;
  }
  .img-demo {
    opacity: 0.6;
    border: 1px solid rgb(0, 17, 71);
