}

@media (max-width: 891px){
    .row{
        display: flex;
        margin-top: 0px;
        flex-direction: column;
    }
    .column-3{
        width: 100%;
    }
    .column-6{
        width: 100%;
    }
    .column-2{
        width: 100%;
        margin-bottom: 50px;
    }
    .contact-image{
        padding: 0px;
    
    }
    .contact-image img{
        width: 100%;
        max-width: 500px;
        padding: 2px;
        border: 1px solid black;
    
    
    }
    .contact-image-caption p{
        font-size: 16px;
        padding: 0px;
        text-align: center;
    }
    .contact-us-form{
        margin: 20px 0;
        padding: 0px;
    }
    .form-control{
        width: 100%;
        padding: 10px;
        margin-bottom: 10px;
        font-family: 'Raleway', sans-serif;
    }



    
}






@media (max-width: 767px){
  #head {
    height: calc(100vh);
    }

    .banner-content h1{
        font-size: 36px;
    }
    
    .banner-content h2{
        font-size: 24px;
    }
    
    .banner-content h3{
        font-size: 18px;
    }
    .footer{
      height: auto;
    }

    .footer-menu {
        display: block;
        padding: 10px;


      
      }

