      }
      .section-heading{
        font-size: 16px;
        padding: 5px;
        color: var(--blue);
        margin: 20px 0px;
        font-family: "Open sans", Helvetica, Arial;
        border-bottom: 2px solid rgb(198, 198, 198);
    }
    .section{
        margin: 15px;
    }
    .para-content{
        font-size: 14px;
    }
    .main-map{
      display: block;
      height: auto;
    }
    .dam-list{
      width: 100%;
      padding: 10px;
      height: auto;
      overflow: scroll;
      max-height: 400px;
      overflow: scroll;

  
    }


    .coordinates_container {
      display: none;
    }
    .right-side{
      width: 100%;
      height: 90vh;
      overflow: scroll;
  }

