@media (max-width: 500px){
    .navbar-container input[type="checkbox"]:checked ~ .logo{
        display: none;
    }
    .risk-analysis-popup h4{
      font-size: 12px;
      padding: 5px;
    }
  
    .risk-analysis-popup img{
  width: 200px;
  
    }
    .leaflet-popup-content-wrapper, .leaflet-popup-tip{
      width: 250px;
    }

    .dam-list-popup h4{
      font-size: 12px;
      padding: 5px;
      background-color: var(--light-blue);
      color: white;
    }
    .dam-list-popup p{
      font-size: 10px;
  
    }
    .dam-list-popup a{
      font-size: 10px;
  
    }
}



