:root {
    --light-blue: rgb(0, 166, 255);
    --dark-blue: rgb(3, 29, 123);
    --blue: #0c52a1;
    --displayHeight:calc(100vh - 114px);
}

a {
    text-decoration: none;
}

.navbar {
    background-color: #002046;
    width: 100%;
    height: 60px;
    display: flex;
    align-items: center;
    z-index: 100000;
    justify-content: space-between;
    top: 0;
    position: fixed;

}

.logo-image img{
    width: 200px;
    /* height: 50px; */
    align-items: center;
    margin: auto;

    /* display: flex; */
    margin: 5px;
}


.nav-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 60px;

}

.nav-logo {
    align-items: center;
    margin-left: 20px;
    flex-grow: 1;

}

.nav-menu {
    display: flex;
    list-style: none;
    text-align: center;
    justify-content: space-between;

}

.nav-links {
    color: white;

    text-decoration: none;
    padding: 10px 10px;
    font-weight: 500;
    font-family: "Open sans", Helvetica, Arial;
    font-size: 18px;
    align-items: center;
}
.active-nav{
    color: white;
    text-decoration: none;
    padding: 10px 10px;
    font-weight: 500;
    font-family: "Open sans", Helvetica, Arial;
    font-size: 18px;
    align-items: center;
    background-color: var(--light-blue);

}

.nav-links:hover {
    background-color: var(--light-blue);

}


.fa-code {
    margin-left: 1rem;
}

.nav-item {
    line-height: 50px;
    margin-right: 0.5rem;
}
.nav-item a{
text-decoration: none;
color: white;
padding: 10px 10px;
font-weight: 500;
font-family: "Open sans", Helvetica, Arial;
font-size: 18px;
align-items: center;

}
.nav-item a:hover{
    background-color: var(--light-blue);
    }


.nav-icon {
    display: none;
}



@media screen and (max-width: 767px) {
    .nav-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        position: absolute;
        top: 60px;
        left: -110%;
        opacity: 1;
        transition: all 0.5s ease;
    }

    .nav-menu.active {
        background-color: #002046;
        left: 0px;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 10000;
    }

    .nav-item {
        margin-right: 0;
    }

    .nav-links {
        padding: 10px;
        width: 100%;
        display: table;
    }
    .nav-item a{
        padding: 10px;
        width: 100%;
        display: table;
        
        }
    .active-nav{
        padding: 10px;
        width: 100%;
        display: table;
    
    }

    .nav-icon {
        display: block;
        position: absolute;
        top: 10;
        right: 20px;
        font-size: 30px;
        cursor: pointer;
        align-items: center;
        color: white;
    }
}